
.back-board.page-back.canvas {
	top: 14vh;
}

#render2d {
	position: absolute;
	top: 14vh;
	z-index: 2;
}

.hot-item {
	cursor: pointer;
}
.hot-item .hot-circle {
	width: 20px;
	height: 20px;
	border-radius: 50%;
	border: 2px solid white;
	margin-right: 10px;
	transition: all 0.3s;
	float: left;
}
.hot-item .hot-label {
	width: 200px;
	font-size: 24px;
	color: white;
	transition: all 0.3s;
	margin-top: -9px;
}

.hot-item:hover .hot-circle {
	transform: scale(1.05);
	border-color: #ff9151;
}
.hot-item:hover .hot-label {
	transform: scale(1.05);
	color: #ff9151;
}
