
.back-board.active.logo-wrapper {z-index: 2;}
.logo-wrapper img {user-select: none;}
.logo-wrapper div {position: absolute;}
.logo-wrapper .img-logo {top: -120px; right: 80vw; display: flex; align-items: center; height: 80px; transition: all 1s;}
.logo-wrapper.show .img-logo {top: 20px;}
.logo-wrapper .img-logo img {height: 100%; width: auto; margin: 0px 10px; cursor: pointer;}

.logo-wrapper .img-cross {width: 25vw; bottom: 0px; left: 0px; opacity: 0; transition: all 1s;-webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)), to(rgba(0,0,0,1)));}
.logo-wrapper.show .img-cross {opacity: 1;}
.logo-wrapper .img-cross img {width: 100%; height: auto;}

.logo-wrapper .img-como {width: 30vw; top: 0px; right: 0px; opacity: 0; transition: all 1s; -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));}
.logo-wrapper.show .img-como {opacity: 1;}
.logo-wrapper .img-como img {width: 100%; height: auto;}

.back-image { overflow: hidden; }
.back-image img { position: absolute; transition: filter 1s; filter: blur(0px);}
.back-image img.blur { filter: blur(8px); }

.back-image .drag-label { position: absolute; width: 100vw; height: 100px; top: calc(50vh - 50px); color: white; font-family: siteThin; font-size: 36px; display: flex; align-items: center; justify-content: center;}
.back-image .drag-circle { position: absolute; width: 60px; height: 60px; top: calc(50vh - 30px); left: calc(50vw - 30px); border-radius: 50px; border: 3px solid #D35827; transition: all 1s;}
.back-image .drag-circle.left { left: calc(50vw - 100px); }
.back-image .drag-circle.right { left: calc(50vw + 40px); }

.trans-page {width: 100vw; height: 100vw; top: -100vw; overflow-y: hidden;  transition: top 5s;  z-index: 2;}
.trans-page.active {top: 100vh;}

.trans-page .trans-back {position: absolute; left: 0; width: 100%;}
.trans-page .trans-back.trans-white {height: 50vh; bottom: 0; background-color: white;}

.trans-page .trans-back.trans-image {height: 120vw; bottom: 25vh; transition: bottom 5s;}
.trans-page .trans-back.trans-image.active {bottom: -10vh;}

.trans-page .trans-back.trans-blue {height: 25vh; bottom: 25vh; background-color: #004973; transition: bottom 5s;}
.trans-page .trans-back.trans-blue.active {bottom: -25vh;}
