body {
	overflow: hidden;
}
@font-face {
	font-family: siteFont;
	src: url("../font/Poppins-Regular.ttf");
}
@font-face {
	font-family: siteThin;
	src: url("../font/Poppins-Thin.ttf");
}
  
* {
	font-family: siteFont;
}
.page-wrapper {
	width: 100vw;
	height: 100vh;
	background-repeat: no-repeat;
	background-position: center;
	overflow: hidden;
}
.back-board {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: -1;
	background: no-repeat center center fixed;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}
.back-board.page-back {
	opacity: 0;
	z-index: -1;
}
.back-board.page-back.active {
	opacity: 1;
	z-index: 1;
}

.content {
	width: 100%;
	height: 100%;
}

.onboarding-video {
	position: fixed;
	right: 0;
	bottom: 0;
	min-width: 100%;
	min-height: 100%;
	z-index: 1;
	transition: filter 1s;
}
.onboarding-video.hide {
	display: none;
	z-index: -1;
}
.trans-wrapper {
	position: absolute;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	z-index: 1;
	/* display: flex;
	align-items: center;
	justify-content: center; */
}
.trans-wrapper img {
	min-width: 100vw;
	min-height: 100vh;
}
.loading-wrapper {
	background-color: rgba(0, 0, 0, 0.1);
	opacity: 0;
	transition: all 0.5s;
}
.loading-wrapper.active {
	opacity: 1;
	z-index: 1;
}
.loading-wrapper .loading-circle {
	position: absolute;
	border: 6px solid rgba(0, 0, 0, 0);
	border-top: 6px solid rgb(221, 144, 29);
	border-radius: 50%;
}
#loading0 {
	top: calc(50% - 45px);
	left: calc(50% - 45px);
	width: 90px;
	height: 90px;
	animation: spin 1.5s linear infinite;
}
#loading1 {
	top: calc(50% - 60px);
	left: calc(50% - 60px);
	width: 120px;
	height: 120px;
	animation: spin 2s linear infinite;
}
#loading2 {
	top: calc(50% - 75px);
	left: calc(50% - 75px);
	width: 150px;
	height: 150px;
	animation: spin 2.5s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
